import React, { useEffect, useState } from "react";
import { Form, Formik, setNestedObjectValues } from "formik";
import Header from "../Header/Header";
import TextField from "../../components/FormInputs/TextField";
import { Card, Modal } from "react-bootstrap";
import { useHistory } from "react-router-dom";
import PasswordField from "../../components/FormInputs/PasswordField";
import * as Yup from "yup";
import { NotificationManager } from "react-notifications";
import { UserLogin, GetUser, ChangePassword } from "./services";
import { HOME, COURSE, COURSE_TEACHER } from '../../constants/pages';
import { 
    getAuth,
    onAuthStateChanged,
    updatePassword
} from 'firebase/auth';
import { db } from "../../services/requests";
import {
    updateDoc,
    doc,
  } from "firebase/firestore";
const initialValues = {
	email: "",
	password: "",
};

const initialChangePasswordForm = {
    newPassword: "",
    confirmPassword: ""
}

const validationLoginSchema = Yup.object().shape({
    email: Yup.string().email("Email không hợp lệ").required("Mời nhập tên khóa học"),
    password: Yup.string().required("Password không được để trống"),
});

const validationChangePasswordSchema = Yup.object().shape({
    newPassword: Yup.string().required("Mời nhập mật khẩu mới"),
    confirmPassword: Yup.string().oneOf([Yup.ref('newPassword'),null],"Nhập lại không trùng với mật khẩu").required("Mời nhập lại mật khẩu"),
});

const Login = () => {
    const [loading, setLoading] = useState(false);
    const [userAuth,setUserAuth] = useState({});
    const [userData, setUserData] = useState({});
	const [newpass, setNewPass] = useState("");
	const [isShow, setShow] = useState(false);
	const [notification, setNotification] = useState("");
	const [notificationNewPass, setNotificationNewPass] = useState("");

    const history = useHistory();

    const auth = getAuth();

    const loginAsync = async (form) => {
        await UserLogin(form.formValues);
        await onAuthStateChanged(auth, async (user) => {
            console.log("user login",user);
            if (user) {
                setUserAuth(user);
                const userInfo = GetUser(user.uid).then((result)=> {
                    console.log("userdata result",result.data());
                    const resultData = result.data();
                    console.log("firstLogin",resultData.firstLogin);
                    if(resultData.firstLogin){
                        setShow(true);
                    } else {
                        if(resultData.role === 0){
                            history.push(COURSE);
                        } else if(resultData.role === 1){
                            history.push(COURSE_TEACHER);
                        } 
                    }
                    setUserData(result.data());
                    
                });
                
            }
        })
    };

    const changePasswordAsync = async (form) => {
        await updatePassword(userAuth, form.formValues.newPassword)
            .then((cred) => {
                setShow(false);
                const courseDoc = doc(db, 'users', userData.uid);
                console.log(courseDoc);

                var result = updateDoc(courseDoc, {
                    firstLogin: false,
                    password: form.formValues.newPassword, 
                });
                console.log("result first login",result);

                if(userData.role === 0){
                    history.push(COURSE);
                } else if(userData.role === 1){
                    history.push(COURSE_TEACHER);
                }
            }).catch((error) => {
                    console.log(error.message);
            });
    };

    return (
        <>
            <Header></Header>
            <div className="container d-flex justify-content-center flex-column align-items-center mt-5">
                <Card>
                    <Card.Header className="text-monospace text-center lead text-danger font-weight-bold">
                        Chào mừng đến với Trung tâm tin học
                    </Card.Header>
                    <Card.Body>
                        <p className="lead text-danger text-center">
                            {notification.length > 0 ? notification : ""}
                        </p>
                        <Formik
                            initialValues={initialValues}
                            enableReinitialize
                            validationSchema={validationLoginSchema}
                            onSubmit={(values) => {
                                setLoading(true);
                                setTimeout(() => {
                                    loginAsync({ formValues: values });
                                    setLoading(false);
                                }, 1000);
                            } }
                        >
                            {({isValid}) => (
                                <Form className="intro-y">
                                    <TextField
                                        name="email"
                                        label="Email"
                                        isrequired="true"
                                    />
                                    <PasswordField
                                        name="password"
                                        label="Password"
                                        isrequired="true"
                                    />
                                    <button
                                        className="btn btn-danger w-25 float-right"
                                        type="submit"
                                        disabled={!isValid || loading}
                                    >
                                        Login{" "}
                                        {loading && (
                                        <img src="/oval.svg" alt="" className="w-4 h-4 ml-2 inline-block" />
                                    )}
                                    </button>
                                </Form>
                            )}
                        </Formik>
                    </Card.Body>
                </Card>
            </div>

            <div className="container">
                <Modal
                    show={isShow}
                    dialogClassName="modal-90w"
                    aria-labelledby="login-modal"
                >
                    <Card>
                        <Card.Header className="text-monospace lead text-danger font-weight-bold">
                            Change password
                        </Card.Header>
                        <Card.Body>
                            <Card.Text>
                                Đây là lần đầu tiên bạn đăng nhập
                                <br />
                                Bạn cần đổi mật khẩu để tiếp tục
                                <br />
                                <p className="lead text-danger text-center">
                                    {notificationNewPass.length > 0 ? notificationNewPass : ""}
                                </p>
                            </Card.Text>

                            <Formik
                                initialValues={initialChangePasswordForm}
                                enableReinitialize
                                validationSchema={validationChangePasswordSchema}
                                onSubmit={(values) => {
                                    setLoading(true);
                                    setTimeout(() => {
                                        changePasswordAsync({ formValues: values });
                                        setLoading(false);
                                    }, 1000);
                                }}
                            >
                                {({isValid}) => (
                                    <Form className="intro-y">
                                        <PasswordField
                                            id="newPassword"
                                            name="newPassword"
                                            label="Nhập mật khẩu mới"
                                            isrequired="true"
                                        />
                                        <PasswordField
                                            name="confirmPassword"
                                            label="Nhập lại mật khẩu"
                                            isrequired="true"
                                        />
{/* 
                                        {error?.error && (
                                            <div className="invalid">{error.message}</div>
                                        )} */}
                                        <button
                                            className="btn btn-danger w-25 float-right"
                                            type="submit"
                                            disabled={!isValid || loading}
                                        >
                                            Save{" "}
                                            {loading && (
                                    <img src="/oval.svg" alt="" className="w-4 h-4 ml-2 inline-block" />
                                )}
                                        </button>
                                    </Form>
                                )}
                            </Formik>
                        </Card.Body>
                    </Card>
                </Modal>
            </div>
        </>
    );
};

export default Login;